.sidebarOption {
    display: flex;
    align-items: center;
    cursor: pointer;
    max-width: 90%;
}

.sidebarOption:hover {
    background-color: var(--app-background);
    border-radius: 30px;
    color: var(--app-color);
    transform: color 100ms ease-out;

}

.sidebarOption > .MuiSvgIcon-root {
    padding: 20px;
}

.sidebarOption > h2 {
    font-weight: 800;
    font-size: 20px;
    margin-right: 20px;
}

.sidebarOption--active {
    color: var(--app-color);
}