.feed {
    flex: 0.45;
    border-right: 1px solid var(--app-background);
    overflow-y: scroll;
}

.feed--header {
    position: sticky;
    top: 0;
    background-color: white;
    z-index: 100;
    border: 1px solid var(--app-background);
    padding: 15px 20px;
}

.feed--header > h2 {
    font-size: 20px;
    font-weight: 800;
}

.feed::-webkit-scrollbar {
    display: none;
}