.tweetBox {
    padding-bottom: 10px;
    border-bottom: 8px solid var(--app-background);
    padding-right: 10px;

}

.tweetBox > form {
    display:flex;
    flex-direction: column;
}

.tweetBox > form > input {
    flex: 1;
    margin-left: 20px;
    border: none;
    outline: none;
}

.tweetBox--input {
    display:flex;
    padding: 20px;
}

.tweetBox--input > input {
    flex: 1;
    margin-left: 20px;
    font-size: 20px;
    border: none;
    outline: none;
}

.tweetBox--tweetButton {
    background-color: var(--app-color) !important;
    border: none !important;
    color: white !important;
    font-weight: 900 !important;
    text-transform: inherit !important;
    border-radius: 30px !important;
    width: 80px;
    height: 40px !important;
    margin-top: 20px !important;
    margin-left: auto !important;
  }

  .tweetBox--imageInput {
      border: none;
      padding: 10px;
  }